@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.countries-list {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
    margin: 0 15px;
}

.countries-list > li {
    border: 2px solid #2b303b;
    height: 150px;
    display: inline-table;
    box-shadow: -8px 9px 0px 0px #2b303b;
}

.countries-list > li > a {
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    padding: 20px;
    height: 100%;
    font-size: 24px;
}

.countries-list > li:hover {
    background-color: #fff;
}

.not-found {
    text-align: center;
    margin: 15px;
    font-size: 24px;
}
.search-field {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    margin: 30px 15px;
}

.search-field > input {
    font-size: 24px;
    padding: 15px;
}

.country-form {
    border: 2px solid #2b303b;
    box-shadow: -8px 9px 0px 0px #2b303b;
    padding: 15px;
    margin-top: 20px;
}

.country-form > .form-group > label {
    display: block;
    font-size: 13px;
}

.country-form > .form-group {
    margin-bottom: 15px;
}

.country-form > .form-group > .error {
    display: block;
    color: red;
}

.country-form button {
    border:  2px solid #2b303b;
    background-color: #fff;
    padding: 10px 20px;
}

.country-form button:hover, .country-form button:focus {
    cursor: pointer;
    background-color: #2b303b;
    color: #fff;
    border:  2px solid #fff;
}
.details {
    padding: 15px;
}

.details > .title {
    font-size: 24px;
}

.details .list-info {
    list-style: none;
    padding: 0;
}

.details .list-info > li {
    border-top: 2px solid #2b303b;
    padding: 15px;
    margin-bottom: 1px;
}

/* Global styles ========= */
* {
  font-family: 'Roboto Slab', serif;
}

body {
  background-color: #eff2f3;
}


